.processHeader {
  height: 64px;
  background: #FFFFFF;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.12);
}

.vertical {
  display: flex;
  align-items: right;
}

.headerContainer {
  height: auto;
  width: 100%;
  display: flex;
  //margin-top: -10px;
  padding: 10px 0 0 0;
  justify-content: space-between;
  background-color: rgb(246, 244, 236);
}

.headerText {
  font-family: var(--app-text-main-font), serif;
  font-size: 14px;
  margin-top: 12px;
  color: var(--app-color-white-lightest);
  font-weight: 600;
}

.processSidebar {
  img {
    transition: transform 0.6s ease;
  }

  cursor: pointer;
  width: 88px;
  height: 72px;
  left: 0px;
  top: 104px;

  background: #F1F1F1;
  border-radius: 0px 16px 16px 0px;
  color: var(--app-color-gray-dark);
  padding: 0 0 0 5px;
  display: flex;
  flex: 1 1 auto;
  justify-content: space-evenly;
  align-items: center;
  align-content: center;
}

.processSidebar2 {
  img {
    transition: transform 0.6s ease;
  }

  background-color: #F1F1F1;
  border-radius: 0px 0px 16px 0px;
  display: flex;
  flex: 1 1 auto;
  justify-content: space-evenly;
  align-items: center;
  align-content: center;
}

.rotate {
  transform: rotate(180deg);
}

.image {
  width: 12px;
  height: 12px;
  margin-left: 15px;
  margin-right: 7px;
}

.modalLabelHeader {
  font-family: var(--app-text-main-font);
  font-size: 18px;
  color: #4A4A4A;
  color: black;
  text-align: center;
  margin-bottom: 20px;
}

.modalLabel {
  font-family: var(--app-text-main-font);
  font-size: 14px;
  color: #4A4A4A;
  text-align: center;
  margin-bottom: 20px;
}

.modalButtons {
  display: flex;
  flex-direction: row;
}

.importArea {
  height: 100px;
  //background: #FFFFFF;
  align-items: center;
  align-content: center;
  justify-content: center;
  padding-top: 10px;
  background-color: rgb(246,244,236);
}

.importText {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
  cursor: grab;
  background-color: rgb(246,244,236);
  img {
    width: 20px;
    height: 20px;
    margin-right: 7px;
  }

  p {
    align-self: center;
    vertical-align: center;
    margin: 0 10px 0 10px;
  }
}


.detailButton {
  width: 50px;

  p {
    display: none;
  }

  &:hover {
    width: 150px;

    p {
      display: flex;
    }

  }
}

.detailButtonTitleContainer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  max-height: 80%;
  margin: 0;

  p {
    height: 20px;
    font-family: var(--app-text-main-font);
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0;
    line-height: 20px;
  }
}